.TodoInsert{
    display: flex;
    background: #495057;
    input{
        background: none;
        outline: none;
        border: none;
        padding: 0.5rem;
        font-size: 1.125rem;
        line-height: 1.5;
        color: #fff;
        &::placeholder{
            color: #dee2e6;
        }
        //버튼을 제외한 영역을 모두차지
        flex:1;
    }
    button {
        background: none;
        outline: none;
        border: none;
        color: #fff;
        background: #868e96;
        padding-left: 1rem;
        padding-right: 1rem;
        &:hover {
            background: #adb5bd;
        }
    }
}